<template>
  <div id="app">
    <div class="login-container" v-if="!token">
      <label for="username">Username</label>
      <input type="text" v-model="username" id="username">
      <label for="password">Password</label>
      <input type="password" v-model="password" id="password" @keyup.enter="onLogin">
      <button class='btn-login' @click="onLogin" >Login</button>
    </div>
    <div class="main-container" v-else>
      <label for="sensor">Sensor</label>
      <select id="sensor"  v-model="sensor">
        <option value="so2">SO2</option>
        <option value="no2">NO2</option>
        <option value="co">CO</option>
      </select>
      <label for="target">Target PPM</label>
      <input type="number" min="-1" max="50" v-model="target" id="target">
      <button class='btn-login' @click="onSetTarget">Submit</button>
      <button class='btn-login btn-reset' @click="onResetTarget">Reset calib</button>
    </div>
  </div>
</template>

<script>
import AppRequest from './requests/AppRequest'
const appRq = new AppRequest('')
export default {
  name: 'App',
  data(){
    return{
      token:null,
      username:"",
      password:"",
      target:"",
      sensor:""
    }
  },
  methods:{
    onLogin(){
      if(this.username.length===0 || this.password.length===0){
        return alert('Please enter username and password!')
      }
      appRq.login(this.username,this.password)
      .then(async(dt)=>{
        // console.log('login res:',dt)
        if(dt.data.code==='000'){
          this.token = dt.data.token
          appRq.setToken(this.token)
          let config = await appRq.getCalib()
          this.target=config.data.target
          this.sensor=config.data.type
        }
        else{
          alert('Wrong username or password!')
        }
      })
      .catch(err=>{
        console.log('Error login:',err)
        alert(err.message)
      })
    },
    onSetTarget(){
      if(this.sensor.length===0 || this.target)
      appRq.setCalib(this.sensor,parseInt(this.target))
      .then(dt=>{
        // console.log('set calib res:',dt)
        if(dt.data.code==='000'){
          
          alert('Set calib successfully!')
          
        }
        else{
          alert('Set calib failed!')
        }
      })
      .catch(err=>{
        alert(err.message)
      })
    },
    onResetTarget(){
      appRq.resetCalib()
      .then(dt=>{
        if(dt.data.code==='000'){
          this.sensor=''
          this.target='-1'
          alert('Reset calib successfully!')
        }
      })
      .catch(err=>{
        alert(err.message)
      })
    }
  }
}
</script>

