import BaseRequest from "./BaseRequest";
export default class AppRequest extends BaseRequest{
    login(username,password){
        const url = '/login'
        const params={
            username,
            password
        }
        return this.post(url,params)

    }
    setCalib(type,target){
        const url = '/calib'
        const params={
            type,
            target
        }
        return this.post(url,params)
    }
    getCalib(){
        const url='/calib'
        return this.get(url)
    }
    resetCalib(){
        const url ='/reset'
        return this.get(url)
    }
}